<template>
  <div>
    <!--  -->
    <typeNav/>
    <div class="main">
      <div class="py-container">
        <!--bread   -->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#" style="color: white"></a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <!--  -->
            <li class="with-x" v-if="searchParams.categoryName">
              {{ searchParams.categoryName }}
              <i @click="removeCategoryName"></i>
            </li>
            <!--  -->
            <li class="with-x" v-if="searchParams.keyword">
              {{ searchParams.keyword }}<i @click="removeKeyword"></i>
            </li>
            <!--  -->
            <li class="with-x" v-if="searchParams.trademark">
              {{ searchParams.trademark.split(":")[1] }}
              <i @click="removeTradeMark"></i>
            </li>
            <!-- -->
            <li class="with-x" v-for="(attrValue, index) in searchParams.props" :key="index">
              {{ attrValue.split(":")[1] }}
              <i @click="removeAttr(index)"></i>
            </li>
          </ul>
        </div>

        <!--selector search-->
        <SearchSelector @trademarkInfo="trademarkInfo" @attrInfo="attrInfo"/>

        <!--details-->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <!---->
              <ul class="sui-nav">
                <li :class="{ active: isOne }" @click="changeOrder('1')">
                  <a><span v-show="isOne" class="iconfont" :class="{ 'icon-up': isAsc, 'icon-down': isDesc }"></span></a>
                </li>
                <li :class="{ active: isTwo }" @click="changeOrder('2')">
                  <a><span v-show="isTwo" class="iconfont" :class="{ 'icon-up': isAsc, 'icon-down': isDesc }"></span></a>
                </li>
              </ul>
            </div>
          </div>
          <!---->
          <div class="goods-list">
            <ul class="yui3-g">
              <li class="yui3-u-1-5" v-for="(good,index) in goodsList" :key="good.id"
                  v-if="good.defaultImg !== '1' && good.defaultImg !== null &&good.defaultImg !== 'http://47.93.148.192:8080/group1/M00/03/84/rBHu8mHYI2mAHNV_AAGdmwKNHNs174.jpg'&&good.id <= 100">
                <div class="list-wrap">
                  <div class="p-img">
                    <router-link :to="`/detail/${good.id}`" v-show="good.id <= 100">
                      <img v-lazy="good.defaultImg"/>
                    </router-link>
                  </div>
                  <div class="price">
                    <strong v-show="good.id <= 100">
                      <em> </em>
                      <i>{{ good.price }}</i>
                    </strong>
                  </div>
                  <div class="attr" v-show="good.id <= 100">
                    <a target="_blank" href="item.html" :title="good.title">{{ good.title }}</a>
                  </div>
                  <div class="commit" v-show="good.id <= 100">
                    <i class="command"> <span>{{ Math.round(good.price - good.price * 0.4) * 2 }}</span> </i>
                  </div>
                  <div class="operate" v-show="good.id <= 100">
                    <a href="success-cart.html" target="_blank" class="sui-btn btn-bordered btn-danger"></a>
                    <a href="javascript:void(0);" class="sui-btn btn-bordered"></a>
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <!---->
          <!--<Pagination  :pageNo="8"  :pageSize="3" :total="91" :continues="5"/>-->
          <Pagination
              :pageNo="searchParams.pageNo"
              :pageSize="searchParams.pageSize"
              :total="total"
              :continues="5"
              @getPageNo="getPageNo"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchSelector from './SearchSelector/SearchSelector'
import {mapState} from 'vuex'
import {mapGetters} from 'vuex'

export default {
  name: 'Search',
  data() {
    //
    return {
      searchParams: {
        //id
        category1Id: "",
        category2Id: "",
        category3Id: "",
        //
        categoryName: "",
        //
        keyword: "",
        //:
        order: "1:desc",
        //
        pageNo: 1,
        //
        pageSize: 15,
        //
        props: [],
        //
        trademark: "",
      },
    };
  },
  components: {
    SearchSelector
  },
  //|mounted
  beforeMount() {
    //
    Object.assign(this.searchParams, this.$route.query, this.$route.params);
  },
  //
  mounted() {
    //searchParams,
    this.getData();
  },
  computed: {
    //mapGetters  gettershomesearchhome
    ...mapGetters(['goodsList']),
    isOne() {
      return this.searchParams.order.indexOf("1") != -1;
    },
    isTwo() {
      return this.searchParams.order.indexOf("2") != -1;
    },
    isAsc() {
      return this.searchParams.order.indexOf("asc") != -1;
    },
    isDesc() {
      return this.searchParams.order.indexOf("desc") != -1;
    },
    //search
    ...mapState({
      total: (state) => state.search.searchList.total,
    }),
  },
  methods: {
    //search
    getData() {
      this.$store.dispatch('getSearchList', this.searchParams);
    },
    //
    removeCategoryName() {
      //
      //
      //undefined
      this.searchParams.categoryName = undefined;
      this.searchParams.category1Id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      //
      this.getData();
      //()
      //queryparams
      if (this.$route.params) {
        this.$router.push({name: "search", params: this.$route.params});
      }
    },
    //
    removeKeyword() {
      //searchParamskeyword
      this.searchParams.keyword = undefined;
      //
      this.getData();
      //Header --- 
      this.$bus.$emit("clear");
      //
      if (this.$route.query) {
        this.$router.push({name: "search", query: this.$route.query});
      }
    },
    //
    removeTradeMark() {
      //
      this.searchParams.trademark = undefined;
      //
      this.getData();
    },
    //
    trademarkInfo(trademark) {
      //1:  "ID:"
      this.searchParams.trademark = `${trademark.tmId}:${trademark.tmName}`;
      //search
      this.getData();
    },
    //
    attrInfo(attr, attrValue) {
      //["ID::"]
      //console.log(attr, attrValue);
      //
      let props = `${attr.attrId}:${attrValue}:${attr.attrName}`;
      // 
      if (this.searchParams.props.indexOf(props) == -1)
        this.searchParams.props.push(props);
      //
      this.getData();
    },
    //removeAttr
    removeAttr(index) {
      //
      this.searchParams.props.splice(index, 1);
      //
      this.getData();
    },
    //
    changeOrder(flag) {
      //flag:li12
      //order
      let originOrder = this.searchParams.order;
      //
      let orginsFlag = originOrder.split(":")[0];
      let originSort = originOrder.split(":")[1];
      //console.log(originOrder , orginsFlag , originSort)
      //
      let newOrder = "";
      //  
      if (flag == orginsFlag) {
        newOrder = `${orginsFlag}:${originSort == "desc" ? "asc" : "desc"}`;
      } else {
        //
        newOrder = `${flag}:${"desc"}`;
      }
      //order
      this.searchParams.order = newOrder;
      //
      this.getData();
    },
    //---
    getPageNo(pageNo) {
      //
      this.searchParams.pageNo = pageNo;
      //console.log(pageNo);
      //
      this.getData();
    },

  },
  //
  watch: {
    //
    $route(newValue, oldValue) {
      //123id123

      //
      //123id123 id
      this.searchParams.category1Id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      //
      Object.assign(this.searchParams, this.$route.query, this.$route.params);
      //ajax
      this.getData();
    },
  },

}
</script>

<style lang="less" scoped>
.main {
  margin: 10px 0;

  .py-container {
    width: 1200px;
    margin: 0 auto;

    .bread {
      margin-bottom: 5px;
      overflow: hidden;

      .sui-breadcrumb {
        padding: 3px 15px;
        margin: 0;
        font-weight: 400;
        border-radius: 3px;
        float: left;

        li {
          display: inline-block;
          line-height: 18px;

          a {
            color: #666;
            text-decoration: none;

            &:hover {
              color: #4cb9fc;
            }
          }
        }
      }

      .sui-tag {
        margin-top: -5px;
        list-style: none;
        font-size: 0;
        line-height: 0;
        padding: 5px 0 0;
        margin-bottom: 18px;
        float: left;

        .with-x {
          font-size: 12px;
          margin: 0 5px 5px 0;
          display: inline-block;
          overflow: hidden;
          color: #eae0e0;
          background: #292727;
          padding: 0 7px;
          height: 20px;
          line-height: 20px;
          border: 1px solid #3c3636;
          white-space: nowrap;
          transition: color 400ms;
          cursor: pointer;

          i {
            margin-left: 10px;
            cursor: pointer;
            font: 400 14px tahoma;
            display: inline-block;
            height: 100%;
            vertical-align: middle;
          }

          &:hover {
            color: #28a3ef;
          }
        }
      }
    }

    .details {
      margin-bottom: 5px;

      .sui-navbar {
        overflow: visible;
        margin-bottom: 0;

        .filter {
          min-height: 40px;
          padding-right: 20px;
          background: #e3dbdb;
          border: 1px solid #000000;
          padding-left: 0;
          border-radius: 0;
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.065);

          .sui-nav {
            position: relative;
            left: 0;
            display: block;
            float: left;
            margin: 0 10px 0 0;

            li {
              float: left;
              line-height: 18px;

              a {
                display: block;
                cursor: pointer;
                padding: 11px 15px;
                color: #0e0e0e;
                text-decoration: none;
              }

              &.active {
                a {
                  background: #2f2a2a;
                  color: #fff;
                }
              }
            }
          }
        }
      }

      .goods-list {
        margin: 30px 0;

        ul {
          display: flex;
          flex-wrap: wrap;

          li {
            height: 100%;
            width: 20%;
            margin-top: 10px;
            line-height: 28px;

            .list-wrap {
              .p-img {
                padding-left: 15px;
                width: 200px;
                height: 235px;

                a {
                  color: #666;

                  img {
                    max-width: 100%;
                    height: auto;
                    vertical-align: middle;
                  }
                }
              }

              .price {
                padding-left: 15px;
                font-size: 20px;
                color: #f5f0f0;

                strong {
                  font-weight: 700;

                  i {
                    margin-left: -5px;
                  }
                }
              }

              .attr {
                padding-left: 15px;
                width: 85%;
                overflow: hidden;
                margin-bottom: 8px;
                min-height: 48px;
                cursor: pointer;
                line-height: 1.8;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;

                a {
                  color: #efe8e8;
                  text-decoration: none;
                  font-size: 13px;
                  transition: all 500ms;
                }

                a:hover {
                  color: #6cb2c6;
                }
              }

              .commit {
                padding-left: 15px;
                height: 22px;
                font-size: 13px;
                color: #f5f0f0;

                span {
                  font-weight: 700;
                  color: #f83636;
                }
              }

              .operate {
                padding: 12px 15px;

                .sui-btn {
                  display: inline-block;
                  padding: 2px 14px;
                  box-sizing: border-box;
                  margin-bottom: 0;
                  font-size: 12px;
                  line-height: 18px;
                  text-align: center;
                  vertical-align: middle;
                  cursor: pointer;
                  border-radius: 0;
                  background-color: transparent;
                  margin-right: 15px;
                }

                .btn-bordered {
                  min-width: 85px;
                  background-color: transparent;
                  border: 1px solid #211f1f;
                  color: #282525;
                  transition: all 500ms;

                  &:hover {
                    border: 1px solid #1a1919;
                    color: #fff !important;
                    background-color: #666;
                    text-decoration: none;
                  }
                }

                .btn-danger {
                  border: 1px solid #211f1f;
                  color: #282525;
                  transition: all 500ms;

                  &:hover {
                    border: 1px solid #1a1919;
                    background-color: #666;
                    color: white !important;
                    text-decoration: none;
                  }
                }
              }
            }
          }
        }
      }

      .page {
        width: 733px;
        height: 66px;
        overflow: hidden;
        float: right;

        .sui-pagination {
          margin: 18px 0;

          ul {
            margin-left: 0;
            margin-bottom: 0;
            vertical-align: middle;
            width: 490px;
            float: left;

            li {
              line-height: 18px;
              display: inline-block;

              a {
                position: relative;
                float: left;
                line-height: 18px;
                text-decoration: none;
                background-color: #fff;
                border: 1px solid #e0e9ee;
                margin-left: -1px;
                font-size: 14px;
                padding: 9px 18px;
                color: #333;
              }

              &.active {
                a {
                  background-color: #fff;
                  color: #e1251b;
                  border-color: #fff;
                  cursor: default;
                }
              }

              &.prev {
                a {
                  background-color: #fafafa;
                }
              }

              &.disabled {
                a {
                  color: #999;
                  cursor: default;
                }
              }

              &.dotted {
                span {
                  margin-left: -1px;
                  position: relative;
                  float: left;
                  line-height: 18px;
                  text-decoration: none;
                  background-color: #fff;
                  font-size: 14px;
                  border: 0;
                  padding: 9px 18px;
                  color: #333;
                }
              }

              &.next {
                a {
                  background-color: #fafafa;
                }
              }
            }
          }

          div {
            color: #333;
            font-size: 14px;
            float: right;
            width: 241px;
          }
        }
      }
    }
  }
}
</style>
