<template>
  <div class="pagination">
    <button :disabled="pageNo == 1" @click="$emit('getPageNo', pageNo - 1)"></button>
    <button v-if="startNumAndEndNum.start >1" @click="$emit('getPageNo', 1)" :class="{ active: pageNo == 1 }">1</button>
    <button v-if="startNumAndEndNum.start >2">...</button>

    <!--page -->
    <button v-for="(page, index) in startNumAndEndNum.end" :key="index" v-if="page >= startNumAndEndNum.start"
            @click="$emit('getPageNo', page)"
            :class="{ active: pageNo == page }">{{ page }}
    </button>


    <button v-if="startNumAndEndNum.end < totalPage -1"></button>
    <button v-if="startNumAndEndNum.end < totalPage" @click="$emit('getPageNo', totalPage)"
            :class="{active:pageNo==totalPage}">{{ totalPage }}
    </button>
    <button :disabled="pageNo == totalPage"
            @click="$emit('getPageNo', pageNo + 1)">
    </button>
    <button style="margin-left: 30px"> {{ total }} </button>
  </div>
</template>

<script>
export default {
  name: "Pagination",
  props: ["pageNo", "pageSize", "total", "continues"],
  //
  computed: {
    //
    totalPage() {
      //
      return Math.ceil(this.total / this.pageSize);
    },
    //[:5]
    startNumAndEndNum() {
      const {continues, pageNo, totalPage} = this;
      //
      let start = 0,
          end = 0;
      //5
      //
      if (continues > totalPage) {
        start = 1;
        end = totalPage;
      } else {
        //55
        //
        start = pageNo - parseInt(continues / 2);
        //
        end = pageNo + parseInt(continues / 2);
        //start0|
        if (start < 1) {
          start = 1;
          //12345
          end = continues;
        }
        //[end]
        if (end > totalPage) {
          end = totalPage;
          start = totalPage - continues + 1;
        }
      }
      return {start, end};
    },
  }
}
</script>

<style lang="less" scoped>
.pagination {
  text-align: center;

  button {
    margin: 0 6px;
    background-color: #e3dbdb;
    color: #171718;
    outline: none;
    padding: 0 4px;
    vertical-align: top;
    display: inline-block;
    font-size: 13px;
    min-width: 40.5px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    border: 0;
    border-radius: 5px;

    &[disabled] {
      color: #959595;
      cursor: not-allowed;
    }

    &.active {
      cursor: not-allowed;
      background-color: #2a2b30;
      color: #fff;
    }
  }
}
</style>