<template>
  <div class="register-container g-container">
    <!--  -->
    <div class="register">
      <h3>
        <span class="go"> <a href="http://localhost:8080/#/login" target="_blank"></a>
        </span>
      </h3>
      <div class="content">
        <label>:</label>
        <input
            placeholder=""
            v-model="phone"
            name="phone"
            v-validate="{ required: true, regex: /^[1](([3][0-9])|([4][0,1,4-9])|([5][0-3,5-9])|([6][2,5,6,7])|([7][0-8])|([8][0-9])|([9][0-3,5-9]))[0-9]{8}$/ }"
            :class="{ invalid: errors.has('phone') }"
        />
        <span class="error-msg">{{ errors.first("phone") }}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input
            placeholder=""
            v-model="code"
            name="code"
            v-validate="{ required: true, regex: /^\d{6}$/ }"
            :class="{ invalid: errors.has('code') }"
        />
        <button class="button_yanZhen" @click="getCode"></button>
        <!--<img ref="code" src="http://182.92.128.115/api/user/passport/code" alt="code">-->
        <span class="error-msg">{{ errors.first("code") }}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input
            placeholder=""
            v-model="password"
            name="password"
            v-validate="{ required: true, regex: /^[0-9A-Za-z]{8,20}$/ }"
            :class="{ invalid: errors.has('password') }"
        />
        <span class="error-msg">{{ errors.first("password") }}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input
            placeholder=""
            v-model="password1"
            name="password1"
            v-validate="{ required: true, is: password }"
            :class="{ invalid: errors.has('password1') }"
        />
        <span class="error-msg">{{ errors.first("password1") }}</span>
      </div>
      <div class="controls">
        <input
            type="checkbox"
            v-model="agree"
            name="agree"
            v-validate="{ required: true, tongyi: true }"
            :class="{ invalid: errors.has('agree') }"
        />
        <span></span>
        <span class="error-msg">{{ errors.first("agree") }}</span>
      </div>
      <div class="btn">
        <button @click="userRegister"></button>
      </div>
    </div>
    <div style="height: 100px">
      <div class="kuang">
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
        <div class="bubble"></div>
      </div>
    </div>

    <!--  -->
    <div class="copyright">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <div class="address" style="margin-bottom: 5px"></div>
      <div class="beian" style="margin-bottom: 5px">ICP12345678</div>
      <div style="padding-bottom: 50px"></div>
    </div>
  </div>
</template>

<script>
import qipao from "@/components/try/qipao/qipao.css"

export default {
  name: 'Register',
  data() {
    return {
      // --
      phone: "",
      //
      code: "",
      //
      password: "",
      //
      password1: "",
      //
      agree: true,
    };
  },
  methods: {
    //
    async getCode() {
      //---
      try {
        //
        const {phone} = this;
        phone && (await this.$store.dispatch("getCode", phone));
        //code[]
        this.code = this.$store.state.user.code;
      } catch (error) {
      }
    },
    //
    async userRegister() {
      const success = await this.$validator.validateAll();
      if(success){
        try {
          // -- 
          const {phone, code, password, password1} = this;
          (phone && code && password == password1) && await this.$store.dispatch('UserRegister', {phone, code, password});
          //
          this.$router.push("/login");
        } catch (error) {
          //
          alert(error.message);
        }
      }
    }
  },

}
</script>

