<template>
  <!--  -->
  <div class="type-nav">
    <div class="container">
      <div @mouseleave="leaveIndex" @mouseenter="enterShow">
        <h2 class="all" style="cursor: pointer ;font-size: 18px;color:#e2dada!important;"></h2>
        <!--  -->
        <transition name="sort">
          <!--  -->
          <div class="sort" v-show="show">
            <!-- + -->
            <div class="all-sort-list2" @click="goSearch">
              <div class="item" v-for="(c1,index) in categoryList" :key="c1.categoryId" v-show="c1.categoryId <= 17">
                <h3
                    :class="{cur:currentIndex==index}"
                    @mouseenter="changeIndex(index)">
                  <a style="cursor: pointer"
                      :data-categoryName="c1.categoryName"
                      :data-category1Id="c1.categoryId"
                  >{{ c1.categoryName }}</a
                  >
                </h3>
                <!--  -->
                <div class="item-list clearfix" v-show="currentIndex === index">
                  <div class="subitem" v-for="(c2,index) in c1.categoryChild" :key="c2.categoryId">
                    <dl class="fore">
                      <dt>
                        <a style="cursor: pointer;color:#e2dada"
                            :data-categoryName="c2.categoryName"
                            :data-category2Id="c2.categoryId"
                        >{{ c2.categoryName }}</a
                        >
                      </dt>
                      <dd>
                        <!--  -->
                        <em v-for="(c3,index) in c2.categoryChild" :key="c3.categoryId">
                          <a style="cursor: pointer;color:#1f220f"
                              :data-categoryName="c3.categoryName"
                              :data-category3Id="c3.categoryId"
                          >{{ c3.categoryName }}</a
                          >
                        </em>
                      </dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
      <nav class="nav allShou Baiye">
        <ul>
          <li @click="TypeNavP"><a></a></li>
          <li @click="TypeNavP"><a></a></li>
          <li @click="TypeNavP"><a></a></li>
          <li @click="TypeNavP"><a></a></li>
          <li @click="TypeNavP"><a></a></li>
          <li @click="TypeNavP"><a></a></li>
          <li @click="TypeNavP"><a></a></li>
          <li @click="TypeNavP"><a></a></li>
        </ul>
      </nav>
    </div>
  </div>
</template>
<script>
//
import {mapState} from 'vuex';
//lodash  lodashthrottle
//
import throttle from "lodash/throttle";
import Baiye from "@/components/try/Baiye.css"
export default {
  name: "TypeNav",
  //
  data() {
    return {
      //
      currentIndex: -1,
      //
      show: true
    };
  },
  mounted() {
    //showfalse :hometypeNav
    if (this.$route.path != "/home") {
      this.show = false;
    }
  },
  //import
  computed: {
    ...mapState({
      //
      //state
      categoryList: (state) => state.home.categoryList,
    }),
  },
  methods: {
    //currentIndex --- htmlindex
    //throttle this
    changeIndex: throttle(function (index) {
      /*
        1.index:
        2.h3
        3.h3 
          
      */
      this.currentIndex = index;
    }, 20),

    //
    goSearch(event) {
      // + 
      //1.<a/>
      //2.[123id]

      //adata-categoryName 
      // event
      let element = event.target;
      //h3adtdldata-categorynamea
      //dataset --adata-categoryName="c1.categoryName"
      //
      let {categoryname, category1id, category2id, category3id} = element.dataset;
      //data-categoryNamea
      if (categoryname) {
        //
        let location = {name: "search"};
        let query = {categoryName: categoryname};
        //a
        if (category1id) {
          query.category1Id = category1id;
        } else if (category2id) {
          query.category2Id = category2id;
        } else {
          query.category3Id = category3id;
        }
        //params
        //search queryparamsqueryparams
        if (this.$route.params) {
          location.params = this.$route.params;
          //
          location.query = query;
          //
          this.$router.push(location);
        }
      }
    },
    //
    leaveIndex() {
      //currentIndex = -1
      this.currentIndex = -1;
      if (this.$route.path !== "/home") {
        this.show = false;
      }
    },
    //
    enterShow() {
      this.show = true;
    },
    //
    TypeNavP(){
      alert(',');
    }
  }
};
</script>

<style scoped lang="less">
.type-nav {
  border-bottom: 2px solid #1f1f1f;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;

    .all {
      width: 210px;
      height: 45px;
      line-height: 50px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        margin: 0 20px;
        padding-left: 15px;
        line-height: 40px;
        font-size: 18px;
        color: #ffffff;

      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 50px;
      width: 210px;
      height: 476px;
      position: absolute;
      background-image: radial-gradient(#8d7979, #211f1f);
      //opacity: 0.9;
      z-index: 999;

      .all-sort-list2 {
        .item {
          h3 {
            line-height: 28px;
            font-size: 14px;
            font-weight: 600;
            overflow: hidden;
            padding: 0 20px;
            margin: 0;

            a {
              color: #e2dada;
            }
          }
          h3:hover{
            font-size: 16px;
          }

          .item-list {
            //display: none;
            position: absolute;
            width: 500px;
            min-height: 476px;
            background-image: radial-gradient(#e9dfdf, #604c4c);
            opacity: 0.9;
            left: 200px;
            border: 1px solid #302b2b;
            top: 0;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eeeeee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                }

                dt {
                  float: left;
                  width: 80px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;
                  font-size: 13px
                }
                dt:hover{
                  font-size: 18px;
                }

                dd {
                  float: left;
                  width: 415px;
                  padding: 3px 0 0;
                  overflow: hidden;

                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    border-left: 1px solid #0a0000;
                    opacity: 1;
                    font-size: 12px;
                    font-weight: bold
                  }
                  em:hover{
                    font-size: 15px;
                  }
                }
              }
            }
          }

          /*
                    &:hover {
                      .item-list {
                        display: block;
                      }
                    }*/
        }

        .cur {
          background: #031c26;
        }
      }
    }
    .allShou{
      a:hover{
        background-color: skyblue;
      }
      a{
        cursor: pointer;
      }
    }

    //
    //
    .sort-enter {
      height: 0px;
      //transform: rotate(0deg);
      opacity: 0;
    }

    //
    .sort-enter-to {
      height: 473px;
      //transform: rotate(360deg);
      opacity: 1;
    }

    //
    .sort-enter-active {
      transition: all .8s linear;
      overflow: hidden;
    }
  }
}
</style>