<template>
  <!--  -->
  <header class="header">
    <!--  -->
    <div class="top">
      <div class="container">
        <div class="loginList">
          <p style="color: #cbc0c0"></p>
          <!--  -->
          <p v-if="!userName" >
            <!-- router-linkto -->
            <router-link to="/login" style="font-size: 15px;color: #cdb9b9"></router-link>
            <router-link class="register" to="/register" style="font-size: 15px;color: #cdb9b9"></router-link>
          </p>
          <!--  -->
          <p v-else>
            <a style="color: #cdb9b9">{{ userName }} </a>
            <a style="color: #cdb9b9" class="register" @click="logout"></a>
          </p>
        </div>
        <div class="typeList">
          <router-link to="/center"></router-link>
          <router-link to="/shopcart"></router-link>
          <a><router-link to="/canvasball1"></router-link></a>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
        </div>
      </div>
    </div>
    <!-- -->
    <div class="bottom">
      <h1 class="logoArea">
        <!-- <router-link class="logo" title="" href="###" target="_blank" to="/home"> -->
        <router-link class="logo" to="/home">
          <h1 class="trylight">&nbsp;&nbsp;</h1>
        </router-link>
      </h1>
      <div class="searchArea">
        <form action="###" class="searchForm">
          <!--  -->
          <input type="text" id="autocomplete" class="input-error input-xxlarge"
                 v-model="keyword"
          />
          <!--  -->
          <button class="night" type="button" @click="goSearch" target="blank">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            
          </button>
        </form>
      </div>
    </div>
  </header>
</template>

<script>
import night from "@/components/try/night.css"
import BaoL from "@/components/try/BaoL/BaoL.css"
export default {
  name: "Header",
  data() {
    return {
      keyword: ''
    }
  },
  methods: {
    //search 
    goSearch() {
      //
      // (this.keyword.toUpperCase() )
      //this.$router.push('/search/' + this.keyword+"?k="+this.keyword.toUpperCase());

      //
      //this.$router.push(`/search/${this.keyword}?k=${this.keyword.toUpperCase()}`)

      //  -- 
      //this.$router.push({name:"search",params:{keyword:this.keyword},query:{k:this.keyword.toUpperCase()}},()=>{},(error)=>{})
      //query,query
      if (this.$route.query) {
        let loction = {name: "search", params: {keyword: this.keyword || undefined}}
        //query
        loction.query = this.$route.query;
        this.$router.push(loction);
      }
      console.log(this.$router);
    },
    //
    async logout() {
      //
      //1:token
      //2:userInfotoken
      try {
        //
        await this.$store.dispatch('userLogout');
        //
        this.$router.push('/home');
      } catch (error) {
        alert(error.message);
      }
    }
  },
  mounted() {
    ////
    this.$bus.$on("clear", () => {
      this.keyword = '';
    })
  },
  computed: {
    //
    userName() {
      return this.$store.state.user.userInfo.name;
    }
  }
}
</script>

<style scoped lang="less">

.header {
  & > .top {
    height: 20px;
    line-height: 20px;

    .container {
      width: 1200px;
      margin: 0 auto;
      overflow: hidden;

      .loginList {
        float: left;

        p {
          float: left;
          margin-right: 10px;

          .register {
            border-left: 1px solid #b3aeae;
            padding: 0 5px;
            margin-left: 5px;
            cursor: pointer;
          }

        }
      }

      .typeList {
        float: right;

        a {
          padding: 0 10px;
          font-size: 15px;
          color: #cdb9b9;

          &
          + a {
            border-left: 1px solid #b3aeae;
          }

        }

      }

    }
  }

  &
  > .bottom {
    width: 1200px;
    height: 100px;
    margin: 0 auto;
    overflow: hidden;

    .logoArea {
      float: left;

      .logo {

        img {
          width: 175px;
          margin: 25px 45px;
        }

      }
    }

    .searchArea {
      float: right;
      margin-top: 35px;

      .searchForm {
        overflow: hidden;

        input {
          box-sizing: border-box;
          width: 530px;
          height: 41px;
          padding: 0px 12px;
          border: 2px solid #ae938f;
          float: left;
          font-size: 15px;

          &
          :focus {
            outline: none;
          }

        }

        button {
          height: 42px;
          width: 78px;
          float: left;
          cursor: pointer;
          &
          :focus {
            //outline: none;
          }

        }
      }
    }
  }
  .trylight{
    margin-top: 10px;
      font-size: 35px;
      text-transform: uppercase;
      letter-spacing: 15px;
      color: rgb(208, 217, 233);
      -webkit-box-reflect: below 1px linear-gradient(transparent , rgb(221, 210, 210));
      animation: san 4s linear infinite;
    @keyframes san{
      0%,15%,50%,52%,70%,90%,99.1%{
        color: rgb(223, 226, 245);
        filter: blur(1px);
      }
      12%,15.1%,60%,70.1%,90.5%,100%{
        color: rgb(255, 255, 255);
        text-shadow: 0 0 3px rgb(255, 255, 255),
        0 0 25px rgb(255, 255, 255),
        0 0 35px rgb(208, 210, 212),
        0 0 105px rgb(93, 98, 107),
        0 0 155px rgb(234, 243, 243);
        filter: blur(1px);
      }
    }
  }
}

</style>
