//
import Home from "@/pages/Home";
import Search from "@/pages/Search";
import Login from "@/pages/Login";
import Register from "@/pages/Register";
import Detail from "@/pages/Detail";
import AddCartSuccess from "@/pages/AddCartSuccess";
import ShopCart from "@/pages/ShopCart";
import Trade from '@/pages/Trade'
import Pay from '@/pages/Pay'
import PaySuccess from '@/pages/PaySuccess/index'
import Center from '@/pages/Center'
//
import MyOrder from '@/pages/Center/myOrder'
import GroupOrder from '@/pages/Center/groupOrder'

export default [
    {
        path: "/home",
        component: Home,
        //meta  metashow
        meta: {show: true}
    },
    {
        path: '/search/:keyword?',
        component: Search,
        meta: {show: true},
        name: 'search',
        props: route => ({keyword3: route.params.keyword, keyword4: route.query.keyword2})
        //props  
        //props:true,

        //
        //props:{a:1,b:2},

        // ---  paramsqueryprops
        /*            props:($route)=>{
                        return{
                            keyword:$route.params.keyword,k:$route.query.k
                        };
                    }*/
    },
    {
        path: "/login",
        component: Login,
        meta: {show: false}
    },
    {
        path: "/register",
        component: Register,
        meta: {show: false}
    },
    //  / 
    {
        path: '*',
        redirect: "/home"
    },
    {
        path: "/detail/:skuid",
        component: Detail,
        meta: {show: true}
    },
    {
        name: 'addcartsuccess',
        path: "/addcartsuccess",
        component: AddCartSuccess,
        meta: {show: true}
    },
    {
        name: 'shopcart',
        path: "/shopcart",
        component: ShopCart,
        meta: {show: true}
    },
    {
        path: '/trade',
        component: Trade,
        /* ,  */
        beforeEnter(to, from, next) {
            if (from.path === '/shopcart') {
                next()
            } else {
                next('/shopcart')
            }
        }
    },
    {
        name: 'pay',
        path: "/pay",
        component: Pay,
        meta: {show: true},
        /* ,  */
        beforeEnter(to, from, next) {
            if (from.path === '/trade') {
                next()
            } else {
                next('/trade')
            }
        }
    },
    {
        name: 'paysuccess',
        path: "/paysuccess",
        component: PaySuccess,
        meta: {show: true}
    },
    {
        name: 'center',
        path: "/center",
        component: Center,
        meta: {show: true},
        //
        children:[
            {
                path:'myorder',
                component:MyOrder,
            },
            {
                path: 'grouporder',
                component: GroupOrder,
            },
            {
              path:'/center',
              redirect:'/center/myorder',
            },
        ]
    },
]